<template>
	<v-tab-item>
		<v-row class="mt-3">
      <v-col 
         v-if="isLoading"
        cols="12" sm="4">
        <Loader type="card" />
      </v-col>
			<v-col 
        v-else
				cols="12" sm="4"
				v-for="(item, i) in dataMentor" :key="i">
				<v-img 
          :src="`http://progresio.id/api/public/uploads/mentor/${item.gambar}`" 
          max-width="100%" 
          max-height="150"
          class="img-container">
          <div class="overlay-img">
            <Edit :data="item" />
            <Hapus :id="item.id" />
          </div>
        </v-img>
				<div class="title black--text">{{ item.nama }}</div>
        <div class="subtitle-1 grey--text">{{ item.posisi }}</div>
			</v-col>
		</v-row>
		<v-dialog
      v-model="dialog"
      width="500"
      persistent>
      <template v-slot:activator="{ on }">
				<v-btn
					v-on="on"
					outlined
					color="primary">
					<v-icon color="primary">mdi-plus-circle</v-icon>
					add mentor
				</v-btn>
      </template>

      <v-card>
        <div class="pa-2 text-center">
          <span class="title">Added Mentor</span>
        </div>
        <v-form v-model="valid" ref="form" enctype="multipart/form-data">
          <v-card-text>
            <v-text-field
              v-model="data.nama"
              label="Name"
              dense
              :rules="requiredRule"
              autocomplete="off"
            ></v-text-field>  
            <v-text-field
              v-model="data.posisi"
              label="Position"
              dense
              :rules="requiredRule"
              autocomplete="off"
            ></v-text-field>   
            <v-file-input
              accept="image/*"
              v-model="data.gambar"
              show-size
              counter
              :rules="requiredRule"
              label="Photo"
            ></v-file-input>  

            <Notif :msg="msg" :status="status" @visible="visible" />
          </v-card-text>

          <v-divider></v-divider>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              color="primary"
              text
              @click="dialog = false">
              kembali
            </v-btn>
            <v-btn
              :disabled="!valid"
              :loading="isLoading"
              color="primary"
              @click.prevent="validate"
              type="submit"
              depressed>
              simpan
            </v-btn>
          </v-card-actions>
        </v-form>
      </v-card>
    </v-dialog>
	</v-tab-item>
</template>

<script>
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'
  import Loader from '@/components/Loader'
  import Edit from './Mentor/Edit'
  import Hapus from './Mentor/Hapus'
  export default {
    data () {
      return {
        dialog: false,
        valid: true,
        data: {
          name: '',
          posisi: '',
          gambar: null
        },
        requiredRule: [v => !!v || 'Data harus diisi'],
        msg: {
          success: 'Berhasil diubah',
          error: 'Gagal diubah',
          visible: false
        }
      }
    },

    components: {
      Hapus,
      Edit,
      Loader,
      Notif
    },

    created() {
      this.$store.dispatch('dataIncubatorMentor/getDataMentor')
      this.$store.dispatch('dataIncubatorMentor/updateIsLoading', true)
    },

    computed: {
      ...mapState('dataIncubatorMentor', ['dataMentor', 'isLoading', 'status']),      
    },

    watch: {
      status(val) {
        if (val === null) this.dialog = false
      }
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          let data = new FormData()
          data.append('nama', this.data.nama)
          data.append('posisi', this.data.posisi)
          data.append('gambar', this.data.gambar)
          this.$store.dispatch('dataIncubatorMentor/postDataMentor', data)
          this.$store.dispatch('dataIncubatorMentor/updateIsLoading', true)
          this.msg.visible = true
        }
      },
      visible(val) {
        this.msg.visible = val
        this.$store.commit('dataIncubatorMentor/updateStatus', null)
      }
    }
  }
</script>