<template>
	<v-dialog
    v-model="dialog"
    width="500"
    persistent>
    <template v-slot:activator="{ on }">
			<v-btn
				v-on="on"
				icon
				class="white mr-3"
				color="primary">
				<v-icon small>mdi-pencil</v-icon>
			</v-btn>
    </template>

    <v-card>
      <div class="pa-2 text-center">
        <span class="title">Edit Tenant</span>
      </div>
      <v-form v-model="valid" ref="form" enctype="multipart/form-data">
        <v-card-text>
          <v-text-field
            v-model="data.nama"
            label="Name"
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>  
          <div class="d-flex mt-3">
            <v-select
              v-model="data.kategori.id"
              :items="detailKategori"
              item-text="nama"
              item-value="id"
              label="Category"
              dense
              :rules="requiredRule"
              autocomplete="off"
            ></v-select>
          </div>  
          <v-file-input
            accept="image/*"
            v-model="logo"
            show-size
            counter
            label="Logo"
          ></v-file-input>   

          <Notif :msg="msg" :status="status" @visible="visible" />
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false">
            kembali
          </v-btn>
          <v-btn
            :disabled="!valid"
            :loading="isLoading"
            color="primary"
            @click.prevent="validate"
            type="submit"
            depressed>
            simpan
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>


<script>
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'
  export default {
    props: ['data'],

    data () {
      return {
        dialog: false,
        valid: true,
        logo: null,
        requiredRule: [v => !!v || 'Data harus diisi'],
        msg: {
          success: 'Berhasil diubah',
          error: 'Gagal diubah',
          visible: false
        }
      }
    },

    components: {
      Notif
    },

    computed: {
      ...mapState('dataIncubatorTenant', ['isLoading', 'status', 'dataKategori']),
      detailKategori: {
				get() {
					return this.dataKategori
				},
				set(val) {
					this.dataKategori.filter(x => x.id === val)
				}
      }
    },

    watch: {
      status(val) {
        if (val === null) this.dialog = false
      }
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          let data = new FormData()
          data.append('id', this.data.id)
          data.append('nama', this.data.nama)
          data.append('kategori_id', this.data.kategori.id)
          if (this.logo !== null) data.append('logo', this.logo)
          this.$store.dispatch('dataIncubatorTenant/putDataTenant', data)
          this.$store.dispatch('dataIncubatorTenant/updateIsLoading', true)
          this.msg.visible = true
        }
      },
      visible(val) {
        this.msg.visible = val
        this.$store.commit('dataIncubatorTenant/updateStatus', null)
      }
    }
  }
</script>