<template>
	<v-tab-item>
		<v-row class="mt-3">
      <v-col 
         v-if="isLoading"
        cols="12" sm="4">
        <Loader type="card" />
      </v-col>
			<v-col 
        v-else
				cols="12" sm="4"
				v-for="(item, i) in dataAward" :key="i">
				<v-img 
          :src="`http://progresio.id/api/public/uploads/award/${item.gambar}`" 
          max-width="100%" 
          max-height="150"
          class="img-container">
          <div class="overlay-img">
            <Edit :data="item" />
            <Hapus :id="item.id" />
          </div>
        </v-img>
				<div class="title black--text">{{ item.nama }}</div>
        <div class="subtitle-1">{{ item.tahun }}</div>
			</v-col>
		</v-row>
		<v-dialog
      v-model="dialog"
      width="500"
      persistent>
      <template v-slot:activator="{ on }">
				<v-btn
					v-on="on"
					outlined
					color="primary">
					<v-icon color="primary">mdi-plus-circle</v-icon>
					add award
				</v-btn>
      </template>

      <v-card>
        <div class="pa-2 text-center">
          <span class="title">Added Award</span>
        </div>
        <v-form v-model="valid" ref="form" enctype="multipart/form-data">
          <v-card-text>
            <v-text-field
              v-model="data.nama"
              label="Name"
              dense
              :rules="requiredRule"
              autocomplete="off"
            ></v-text-field> 
            <v-dialog
              ref="dialog"
              v-model="modal"
              :return-value.sync="data.tahun"
              persistent
              width="290px">
              <template v-slot:activator="{ on }">
                <v-text-field
                  v-model="data.tahun"
                  label="Picker in dialog"
                  prepend-icon="mdi-calendar"
                  readonly
                  v-on="on"
                  :rules="requiredRule"
                ></v-text-field>
              </template>
              <v-date-picker v-model="data.tahun" scrollable>
                <v-spacer></v-spacer>
                <v-btn text color="primary" @click="modal = false">Cancel</v-btn>
                <v-btn text color="primary" @click="$refs.dialog.save(data.tahun)">OK</v-btn>
              </v-date-picker>
            </v-dialog> 
            <!-- <v-text-field
              v-model="data.tahun"
              label="Date (yyyy-mm-dd)"
              dense
              :rules="dateRule"
              autocomplete="off"
            ></v-text-field>   --> 
            <v-file-input
              accept="image/*"
              v-model="data.gambar"
              show-size
              counter
              :rules="requiredRule"
              label="Documentation"
            ></v-file-input>         

            <Notif :msg="msg" :status="status" @visible="visible" />
          </v-card-text>

          <v-divider></v-divider>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              color="primary"
              text
              @click="dialog = false">
              kembali
            </v-btn>
            <v-btn
              :disabled="!valid"
              :loading="isLoading"
              color="primary"
              @click.prevent="validate"
              type="submit"
              depressed>
              simpan
            </v-btn>
          </v-card-actions>
        </v-form>
      </v-card>
    </v-dialog>
	</v-tab-item>
</template>

<style scoped>
  .overlay-img{
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    background-color: rgba(0,0,0,0.3);
    visibility: hidden;
    transition: visibility 0.2s ease;    
  }
  .img-container:hover .overlay-img{
    visibility: visible;
  }
</style>

<script>
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'
  import Loader from '@/components/Loader'
  import Edit from './Award/Edit'
  import Hapus from './Award/Hapus'

  export default {
    data () {
      return {
        dialog: false,
        valid: true,
        modal: false,
        data: {
          nama: '',
          tahun: '',
          gambar: null
        },
        requiredRule: [v => !!v || 'Data harus diisi'],
        msg: {
          success: 'Berhasil diubah',
          error: 'Gagal diubah',
          visible: false
        }
      }
    },

    components: {
      Hapus,
      Edit,
      Loader,
      Notif
    },

    created() {
      this.$store.dispatch('dataIncubatorAward/getDataAward')
      this.$store.dispatch('dataIncubatorAward/updateIsLoading', true)
    },

    computed: {
      ...mapState('dataIncubatorAward', ['dataAward', 'isLoading', 'status']),
      dateRule () {
        return [
          ...this.requiredRule,
          v => !!v.match(/^(19[5-9][0-9]|20[0-4][0-9]|2050)[-/](0?[1-9]|1[0-2])[-/](0?[1-9]|[12][0-9]|3[01])$/igm) || 'Harus berupa tanggal'
        ]
      }
    },

    watch: {
      status(val) {
        if (val === null) this.dialog = false
      }
    }, 

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          let data = new FormData()
          data.append('nama', this.data.nama)
          data.append('tahun', this.data.tahun)
          data.append('gambar', this.data.gambar)
          this.$store.dispatch('dataIncubatorAward/postDataAward', data)
          this.$store.dispatch('dataIncubatorAward/updateIsLoading', true)
          this.msg.visible = true
        }
      },
      visible(val) {
        this.msg.visible = val
        this.$store.commit('dataIncubatorAward/updateStatus', null)
      }
    }
  }
</script>