<template>
	<v-tab-item>
		<v-row class="mt-3">
			<v-col 
        v-if="isLoading"
				cols="12" sm="4">
        <Loader type="card" />
      </v-col>
      <v-col 
        v-else
        cols="12" sm="4"
        v-for="(item, i) in dataTenant" :key="i">
				<v-img 
          src="@/assets/se.png" 
          max-width="100%" 
          max-height="150"
          class="img-container">
          <div class="overlay-img">
            <Edit :data="item" />
            <Hapus :id="item.id" />
          </div>
        </v-img>
				<div class="title black--text">{{ item.nama }}</div>
        <div class="subtitle-1 grey--text">{{ item.kategori.nama }}</div>
			</v-col>
		</v-row>
		<v-dialog
      v-model="dialog"
      width="500"
      persistent>
      <template v-slot:activator="{ on }">
				<v-btn
					v-on="on"
					outlined
					color="primary">
					<v-icon color="primary">mdi-plus-circle</v-icon>
					add tenant
				</v-btn>
      </template>

      <v-card>
        <div class="pa-2 text-center">
          <span class="title">Added Tenant</span>
        </div>
        <v-form v-model="valid" ref="form">
          <v-card-text>
            <v-text-field
              v-model="data.nama"
              label="Name"
              dense
              :rules="requiredRule"
              autocomplete="off"
            ></v-text-field>  
            <div class="d-flex mt-3">
              <v-select
                v-model="data.kategori_id"
                :items="dataKategori"
                item-text="nama"
                item-value="id"
                label="Category"
                dense
                :rules="requiredRule"
                autocomplete="off"
              ></v-select> 
              <Kategori />
            </div>  
            <v-file-input
              accept="image/*"
              v-model="data.logo"
              show-size
              counter
              :rules="requiredRule"
              label="Logo"
            ></v-file-input>   

            <Notif :msg="msg" :status="status" @visible="visible" />
          </v-card-text>

          <v-divider></v-divider>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              color="primary"
              text
              @click="dialog = false">
              kembali
            </v-btn>
            <v-btn
              :disabled="!valid"
              :loading="isLoading"
              color="primary"
              @click.prevent="validate"
              type="submit"
              depressed>
              simpan
            </v-btn>
          </v-card-actions>
        </v-form>
      </v-card>
    </v-dialog>
	</v-tab-item>
</template>

<style>
  .overlay-img{
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    background-color: rgba(0,0,0,0.3);
    visibility: hidden;
    transition: visibility 0.2s ease;    
  }
  .img-container:hover .overlay-img{
    visibility: visible;
  }
</style>

<script>
  import Kategori from './Kategori'
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'
  import Loader from '@/components/Loader'
  import Edit from './Tenant/Edit'
  import Hapus from './Tenant/Hapus'
  export default {
    data () {
      return {
        dialog: false,
        valid: true,
        data: {
          nama: '',
          kategori_id: '',
          logo: null
        },
        requiredRule: [v => !!v || 'Data harus diisi'],
        msg: {
          success: 'Berhasil diubah',
          error: 'Gagal diubah',
          visible: false
        }
      }
    },

    components: { 
      Hapus,
      Edit,
      Kategori,
      Loader,
      Notif
    },

    created() {
      this.$store.dispatch('dataIncubatorTenant/getDataKategoriTenant')
      this.$store.dispatch('dataIncubatorTenant/getDataTenant')
      this.$store.dispatch('dataIncubatorTenant/updateIsLoading', true)
    },

    computed: {
      ...mapState('dataIncubatorTenant', ['dataTenant', 'isLoading', 'status', 'dataKategori'])
    },

    watch: {
      status(val) {
        if (val === null) this.dialog = false
      }
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          let data = new FormData()
          data.append('nama', this.data.nama)
          data.append('kategori_id', this.data.kategori_id)
          data.append('logo', this.data.logo)
          this.$store.dispatch('dataIncubatorTenant/postDataTenant', data)
          this.$store.dispatch('dataIncubatorTenant/updateIsLoading', true)
          this.msg.visible = true
        }
      },
      visible(val) {
        this.msg.visible = val
        this.$store.commit('dataIncubatorTenant/updateStatus', null)
      }
    }
  }
</script>